body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "SummerFire";
  src: url(/static/media/SummerFire.9367384d.eot) format("embedded-opentype");
  src: url(/static/media/SummerFire.9367384d.eot?#iefix) format("embedded-opentype"), url(/static/media/SummerFire.08f5313f.woff2) format("woff2"), url(/static/media/SummerFire.7ed962e1.woff) format("woff"), url(/static/media/SummerFire.e12ddbaa.ttf) format("truetype");
  font-weight: normal;
  font-style: normal; }

.box {
  display: inline-grid; }

.start {
  position: relative;
  top: 0rem;
  background-color: dimgray;
  height: 5rem;
  width: 13rem;
  font-size: 3.2rem;
  color: aliceblue;
  border: solid 4px;
  border-radius: 6px; }

.how {
  position: relative;
  top: 1rem;
  background-color: dimgray;
  height: 5rem;
  width: 13rem;
  font-size: 2.2rem;
  color: aliceblue;
  border: solid 4px;
  border-radius: 6px; }

.title {
  position: relative;
  font-family: "SummerFire";
  font-size: 11rem; }

.gif {
  position: relative;
  left: 10rem;
  border-radius: 9px;
  height: auto;
  width: 16rem; }

.background {
  background-color: dimgray;
  position: fixed;
  background-size: cover;
  z-index: -1;
  min-width: 120rem;
  min-height: 71rem;
  bottom: 0rem;
  right: 0.009rem; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container {
  position: relative;
  bottom: 11rem;
  display: flex;
  font-size: 0.7vw; }

.search {
  position: fixed;
  top: 0.5rem;
  left: 50rem; }

.score {
  position: fixed;
  top: 3.1em;
  left: 1em;
  font-size: 1.6rem;
  color: aliceblue; }

.timer {
  position: fixed;
  top: 5em;
  left: 1em;
  font-size: 1.6rem;
  color: aliceblue; }

.retry {
  top: 5rem;
  right: 2rem;
  position: fixed;
  background-color: dimgray;
  height: 3rem;
  width: 7rem;
  font-size: 1.2rem;
  color: aliceblue;
  border: solid 4px;
  border-radius: 6px; }

.back {
  background-color: dimgray;
